.journal__main-context {
  display: flex;
}

.journal__sidebar {
  background-color: $dark-grey;
  color: $white;
  display: flex;
  flex-direction: column;
  height: 100vh;
  padding-left: 10px;
  padding-right: 10px;
  width: 500px;
}
.journal__sidebar-navbar {
  display: flex;
  justify-content: space-between;

  & h3 {
    font-weight: lighter;
  }
}

.journal__entri {
  align-items: center;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 30px;
  width: 100%;
  transition: color 0.3s ease;
  -webkit-transition: color 0.3s ease;
  -moz-transition: color 0.3s ease;
  -ms-transition: color 0.3s ease;
  -o-transition: color 0.3s ease;
  &:hover {
    color: darken($color: $white, $amount: 20);
  }
}
.journal__entries {
  flex: 1 1 auto;
  margin-top: 10px;
  overflow-y: scroll;
}
.journal__entry {
  background-color: $white;
  border-radius: 4px;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  -ms-border-radius: 4px;
  -o-border-radius: 4px;
  color: $dark-grey;
  display: flex;
  margin-bottom: 10px;
  cursor: pointer;
  margin-right: 10px;
}
.journal__entry-picture {
  height: 75px;
  width: 75px;
}
.journal__entry-body {
  padding: 10px;
}
.journal__entry-title {
  font-style: 14px;
  font-weight: bold;
}
.journal__entry-content {
  font-style: 10px;
}
.journal__entry-date-box {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 5px;

  & span {
    font-style: 12px;
  }
}
